import React from "react";
import { Switch, Route, BrowserRouter, Redirect } from "react-router-dom";
import { Notifications } from "react-push-notification";
import Layout from "./pages/Layout/Layout";
import ActiveOrdersPage from "./pages/ActiveOrdersPage/ActiveOrdersPage";
import EndedOrdersPage from "./pages/EndedOrdersPage/EndedOrdersPage";
import Login from "./pages/Login/Login";
import { getAdminToken } from "./utils/utils";

function App() {
  const PrivateRoute = ({
    redirectTo,
    component: Component,
    condition,
    state = {},
    ...rest
  }) => (
    <Route {...rest}>
      {condition ? (
        <Component />
      ) : (
        <Redirect to={{ pathname: redirectTo, state }} />
      )}
    </Route>
  );
  const aToken = getAdminToken();
  return (
    <BrowserRouter history={{}}>
      <Notifications />
      <Switch>
        <Route exact path="/login" component={Login} />
        <PrivateRoute
          path="/home"
          component={Layout}
          exact
          condition={aToken}
          redirectTo="/login"
        />
        <PrivateRoute
          path="/active-orders"
          component={ActiveOrdersPage}
          condition={aToken}
          redirectTo="/login"
        />
        <PrivateRoute
          path="/ended-orders"
          component={EndedOrdersPage}
          condition={aToken}
          redirectTo="/login"
        />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
